import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../../custom/http/custom-operation/custom-operation.http.service';
import { UpdateLoanApplyRegisInfoBean } from './bean/updateLoanApplyRegisInfo.bean';
import { ConfirmDialogModule, ConfirmationService, Message, SelectItem } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DecimalPipe } from '@angular/common';
@Component({
    selector: 'updateloan',
    templateUrl: './updateloan.component.html',
    styleUrls: ['./updateloan.component.scss'],
    providers: [CusOperationHttpService, ConfirmationService, DecimalPipe]
})
export class UpdateLoanComponent implements OnInit {
    code:any;
    danbaoOptions: any = [];
    listMoney: any;
    listTime: any;
    parentValue: string;
    treePage: boolean = false;
    treeDate: any = [];
    temp: any;
    msgs: Message[] = [];
    guaranteeMode: any ;//主担保方式
    sourceTypeOptions: any ;//来源类型
    applyStatusOptions: any ;//申请状态
    YingxiaoTJOptions: any ;//营销途径

    sourceChanelOptions: any ;//来源渠道

    operateName: any;
    managerName: any;
    operateOrgIdName: any//修改
    introduceshow: boolean = true;//介绍人
    explainUpdate: boolean = false;
    XYTJUpdate: boolean = false;
    ch: any = CALENDAR_CN;
    date: Date = new Date();
    itemsUpdate: any;
    paramUpdate: any;
    startCridetDateUpdate: any;//修改起贷日期
    endCridetDateUpdate: any;//修改到期日期
    operateDateUpdate: any;//修改经办日期
    updatefrom: FormGroup;
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    applyAmt: string;
    updateLoanApplyRegisInfoBean: UpdateLoanApplyRegisInfoBean = new UpdateLoanApplyRegisInfoBean();
    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public routeInfo: ActivatedRoute,
        public router: Router,
        public decimalPipe: DecimalPipe
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        this.updatefrom = fb.group({
            custNo: ['', Validators.required],
            custName: ['', Validators.required],
            startCridetDateUpdate: [''],
            endCridetDateUpdate: [''],
            seqNo: [''],
            sourceChanel: [''],
            intrCustNo: [''],
            intrUserName: [''],
            intrGlobalId: [''],
            intrMobileNo: [''],
            discription: [''],
            marketType: [''],
            applyAmt: ['', Validators.required],
            guaranteeMode: ['', Validators.required],
            managerName: ['', Validators.required],
            operateName: ['', Validators.required],
            operateOrgIdName: ['', Validators.required],
            operateDateUpdate: ['', Validators.required],
            applyStatus: ['', Validators.required],
            sourceType: ['', Validators.required],
            itemsUpdate: ['', Validators.required],
        })
        this.operateDateUpdate = this.date;
    }

    ngOnInit() {
        this.codeValues();
        this.guaranteeMode = this.code['GuaranteeModes'];
        this.sourceTypeOptions = this.code['SourceType'];
        this.sourceChanelOptions = this.code['SourceChanel'];
        this.applyStatusOptions = this.code['ApplyStatus'];
        this.YingxiaoTJOptions = this.code['YingxiaoTJ'];
        this.temp = this.routeInfo.snapshot.params
        this.updatechongzhi();

    }
    updatechanpin() {
        this.treePage = true;
    }
    money() {
        this.applyAmt = this.decimalPipe.transform(this.updateLoanApplyRegisInfoBean.applyAmt, '1.2-2');
        if (!this.listMoney) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请先选择产品' }];
        } else {
            if (parseInt(this.updateLoanApplyRegisInfoBean.applyAmt) < this.listMoney[0].min * 1) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '不能小于' + this.listMoney[0].min }];
            } else if (parseInt(this.updateLoanApplyRegisInfoBean.applyAmt) > this.listMoney[0].max * 1) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '不能大于' + this.listMoney[0].max }];
            }
        }
    }
    method() {
        console.log(this.applyAmt);
        this.updateLoanApplyRegisInfoBean.applyAmt = this.applyAmt;
    }
    transDate(value) {
        if (!value) {
            return ''
        }
        let time = new Date(value);
        let year = value.getFullYear();
        let month = value.getMonth() + 1 + '';
        let data = value.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (data.length < 2) {
            data = "0" + data
        }
        let newtime = year + month + data;
        return newtime;
    }
    showData() {
        if (this.transDate(this.operateDateUpdate) < this.transDate(this.date)) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '不可早于当前日期' }];
            this.operateDateUpdate = '';
            return;
        }
    }
    showDataEnd() {
        let maxtime = this.listTime[0].max * 1;
        let mintime = this.listTime[0].min * 1;
        let endmintime = moment().add(mintime / 12, 'year').format('Y-MM-DD');
        let endmaxtime = moment().add(maxtime / 12, 'year').format('Y-MM-DD');
        if (this.transDate(this.endCridetDateUpdate) < this.transDate(this.startCridetDateUpdate)) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '必须大于起贷日期' }];
            this.endCridetDateUpdate = '';
            return;
        } else if (this.transDate(this.endCridetDateUpdate) > endmaxtime) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '不能大于' + endmaxtime }];
            this.endCridetDateUpdate = '';
            return;
        } else if (this.transDate(this.endCridetDateUpdate) < endmintime) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '不能小于' + endmintime }];
            this.endCridetDateUpdate = '';
            return;
        }
    }
    updatechoose1() {
        if (this.updateLoanApplyRegisInfoBean.sourceType == '01') {
            this.introduceshow = true;
            this.XYTJUpdate = false;
            this.explainUpdate = false;
        } else if (this.updateLoanApplyRegisInfoBean.sourceType == '04') {
            this.XYTJUpdate = true;
            this.introduceshow = false;
            this.explainUpdate = false;
        } else if (this.updateLoanApplyRegisInfoBean.sourceType == '05') {
            this.explainUpdate = true;
            this.introduceshow = false;
            this.XYTJUpdate = false;
        } else {
            this.explainUpdate = false;
            this.introduceshow = false;
            this.XYTJUpdate = false;
        }
    }
    //修改保存
    updateSave() {
        //校验的
        for (const i in this.updatefrom.controls) {
            this.updatefrom.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.updatefrom.controls) {
            if (!this.updatefrom.controls[i].valid) {
                return;
            }
        }
        this.updateLoanApplyRegisInfoBean.bussType = this.paramUpdate;
        this.updateLoanApplyRegisInfoBean.startCridetDate = this.commfunc.transDate(this.startCridetDateUpdate);//修改起贷日期
        this.updateLoanApplyRegisInfoBean.endCridetDate = this.commfunc.transDate(this.endCridetDateUpdate);//修改到期日期
        this.updateLoanApplyRegisInfoBean.operateDate = this.commfunc.transDate(this.operateDateUpdate);//修改经办日期
        this.httpService.saveLoanApplyRegisInfo(this.updateLoanApplyRegisInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: 'Success Message', detail: '修改成功' });
                this.router.navigate(["pages/tzb/workflow/custom-workflow/loans"]);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }
    updateSubmit() {
        //校验的
        for (const i in this.updatefrom.controls) {
            this.updatefrom.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.updatefrom.controls) {
            if (!this.updatefrom.controls[i].valid) {
                return;
            }
        }
        this.updateLoanApplyRegisInfoBean.bussType = this.paramUpdate;
        this.updateLoanApplyRegisInfoBean.startCridetDate = this.commfunc.transDate(this.startCridetDateUpdate);//修改起贷日期
        this.updateLoanApplyRegisInfoBean.endCridetDate = this.commfunc.transDate(this.endCridetDateUpdate);//修改到期日期
        this.updateLoanApplyRegisInfoBean.operateDate = this.commfunc.transDate(this.operateDateUpdate);//修改经办日期
        this.httpService.commitLoanApplyRegisInfo(this.updateLoanApplyRegisInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: 'Success Message', detail: '提交成功' });
                this.router.navigate(["pages/tzb/workflow/custom-workflow/loans"]);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    updateCancel() {
        this.router.navigate(["pages/tzb/workflow/custom-workflow/loans"])
    }
    updatechongzhi() {
        let param = { "seqNo": this.temp.seqNo };
        this.httpService.selectLoanApplyRegisInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.startCridetDate) {
                    this.startCridetDateUpdate = new Date(data.startCridetDate);
                } else {
                    this.startCridetDateUpdate = '';
                }
                if (data.endCridetDate) {
                    this.endCridetDateUpdate = new Date(data.endCridetDate);
                } else {
                    this.endCridetDateUpdate = '';
                }
                if (data.operateDate) {
                    this.operateDateUpdate = new Date(data.operateDate);
                } else {
                    this.operateDateUpdate = '';
                }
                this.updateLoanApplyRegisInfoBean = data;
                this.operateOrgIdName = data.operateOrgIdName;
                this.updateLoanApplyRegisInfoBean.operateOrgId = data.operateOrgId;
                this.updateLoanApplyRegisInfoBean.managerId = data.managerId;
                this.managerName = data.managerIdName;
                this.operateName = data.operateIdName;
                this.updateLoanApplyRegisInfoBean.operateId = data.operateId;
                this.paramUpdate = data.bussType;
                let temp = {
                    productId: data.bussType
                }
                this.httpService.getAvailableProduct(temp).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.itemsUpdate = data.availableProduct.productName;
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                });
                this.updatechoose1();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    getData(event) {
        this.treePage = false;
        this.parentValue = event;
        this.itemsUpdate = this.parentValue[0]['productName'];
        this.paramUpdate = this.parentValue[0]['productId'];
        let arrDanbao = this.parentValue[3];
        this.danbaoOptions = [];
        this.danbaoOptions.push({ label: '请选择', value: '' })
        for (let item of arrDanbao) {
            this.danbaoOptions.push({ label: item['productFeatureName'], value: item['productFeatureId'] })
        }
        this.listMoney = this.parentValue[1];
        this.listTime = this.parentValue[2];
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}