import { Component, OnInit } from '@angular/core';
import { SelectItem, Message, ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { CustGroupBean, CustGroupInfoBean } from './../../../custom/custom-group/bean/custom-group.bean';
import { CusOperationHttpService } from './../../../custom/http/custom-operation/custom-operation.http.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';
import { CustGroupStatusBean } from './bean/updateCustGroupStatus.bean';
import { ZoneInfoBean } from './bean/ZoneInfoBean.bean';
import { ParentZoneInfoBean } from './bean/queryParentZoneInfo.bean';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD, TZB_HTTP_YXKQ } from 'app/@uisftech/common/service/http.constant';
import { CustGroupListBean } from '../../../custom/custom-marketing/group/bean/custom-group.bean';



@Component({
    selector: 'app-create-custom-group',
    templateUrl: './custom-group.component.html',
    styleUrls: ['./custom-group.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CreateCustomGroupApprovalComponent implements OnInit {

    property: boolean = true;
    title: string = '客户群创建审核';
    title1: string = '客户群名单批量导入';
    title2: string = '客户群名单导入结果';
    UPLOAD: string = TZB_CUS_UPLOAD;//上传模板
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载模板
    fileList: any = [];//上传文件列表
    files: any = [];//
    custGroupListBean: CustGroupListBean = new CustGroupListBean();//客户群名单
    batchDisplay: boolean = false;//批量导入
    addDisplay: boolean = false;//返回结果

    // 中间量
    public contactRoleName: any = []; //群联系人
    public introducerRoleName: any = []; //群介绍人
    public mentorRoleName: any = []; //指导师傅
    public contactArr: any = [];//群联系人
    public introducerArr: any = [];//群介绍人
    public mentorRoleArr: any = [];//指导师傅
    // 模态框
    GroupAdminInfoTitle: any = '';//标题
    GroupAdminInfoDisplay: boolean = false;
    public GroupAdminFlag: any = '';
    public groupAdminName: any = '';
    public nameInValue: any = {
        ownerName: '',
    };
    public RoleName: any = '';

    // bean
    public custGroupBean: CustGroupBean = new CustGroupBean();
    public custGroupInfoBean: CustGroupInfoBean = new CustGroupInfoBean();
    public custGroupStatusBean: CustGroupStatusBean = new CustGroupStatusBean();
    public zoneInfoBean: ZoneInfoBean = new ZoneInfoBean();//四级区划信息查询
    public parentZoneInfoBean: ParentZoneInfoBean = new ParentZoneInfoBean();//四级区划解析查询



    // 中间量
    public contactList: any;
    public mentorList: any;
    public scheduleId: any = '';//审批接收业务种类编号
    public taskCategoryId: any = '';//审批接收待办Id
    public list: any = '';//审批接收data

    // 四级区划
    public parentZoneInfoTempArr: any = [];
    public regionArrTemp: any = {};//四级区划
    public count: any = 0;
    public regionSHeng: any = [];
    public regionCity: any = [];
    public regionArea: any = [];
    public regionStreet: any = [];
    public regionSHeng1: any = '';//一级
    public regionCity1: any = '';//二级
    public regionArea1: any = '';//三级
    public regionStreet1: any = '';//四级
    public parentZoneInfoTempNum: any = 4;//四级区划--查询--解析--标志
    public queryZoneInfoTempNum: any = 0;//四级区划--查询--信息--标志

    firstToinvestTradeOptions: any[];//投向行业一级
    secondToinvestTradeOptions: any[];//投向行业二级
    thirdToinvestTradeOptions: any[];//投向行业三级
    fourthToinvestTradeOptions: any[];//投向行业四级
    firstToinvestTrade: any;//投向一
    secondToinvestTrade: any;//投向二
    thirdToinvestTrade: any;//投向三
    fourthToinvestTrade: any;//投向四

    // 其他
    public groupType: any;
    public groupID: any;//群号
    public spyj: any;//审批意见
    public approvalResult: any = '';//审批结果
    // public seqNo: any;//流水号
    public groupId: any;//群号

    // 控件
    public msgs: Message[] = [];
    public param: any;
    code: any;
    opFlag: boolean = false;
    deleteReason: string;
    taskCode: any;
    globalSeqNoImage: string;//影像流水号

    constructor(
        public route: ActivatedRoute,
        public httpService: CusOperationHttpService,
        public router: Router,
        public routerInfo: ActivatedRoute,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues();
        this.groupType = this.code['GroupTypeSave']
    }

    ngOnInit() {
        this.selectCustGroupInfo();
        this.preSet();
    }
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    preSet() {

        if (this.routerInfo.snapshot.params['data']) {
            this.list = JSON.parse(this.routerInfo.snapshot.params['data']);
        }

        if (this.routerInfo.snapshot.params['markFlag'] && this.routerInfo.snapshot.params['markFlag'] == '2') {
            this.property = false;
        }

        this.taskCode = this.routerInfo.snapshot.params['taskCode'];
        this.groupId = this.routerInfo.snapshot.params['groupId'];
        this.globalSeqNoImage = this.routerInfo.snapshot.params['globalSeqNoImage'];

        // 非主键
        this.custGroupInfoBean.groupName = '';//查询name


        // 初始化
        //     if (this.routerInfo.snapshot.params.data) {
        //         this.scheduleId = this.routerInfo.snapshot.params.scheduleId
        //         this.taskCategoryId = this.routerInfo.snapshot.params.taskCategoryId
        //         this.list = JSON.parse(this.routerInfo.snapshot.params.data);
        //     }
        //     // 查询
        this.custGroupInfoBean.groupId = this.list.groupId;//查询id


        // 审批
        this.custGroupStatusBean.groupId = this.list.groupId;//审批id        
        this.custGroupStatusBean.applyType = this.list.applyType;//申请类型

        this.custGroupStatusBean.scheduleId = this.scheduleId;
        this.custGroupStatusBean.taskCategoryId = this.list.taskCategoryId;//??


        // // 群主 session
        // let session = window.sessionStorage;
        // let data = session.getItem('commonHeader');
        // if (!data) {
        //     this.router.navigate(["/login"]);
        //     return;
        // }
        // let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        // if (!mesg) {
        // }
        // let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));


        // 默认
        this.custGroupBean.bgcFlag = '0';//后台中心默认

        //四级区划
        this.zoneInfoBean.parentZoneId = '';//四级区划
    }
    toCustGroupView() {
        let a = 'groupId=' + this.custGroupInfoBean.groupId;
        window.open('/#/pages/tzb/custom/custom-group/custom-group-view;' + a);
    }

    //名单附件下载
    listAttachDownload() {
        // let a = encodeURI(this.custGroupInfoBean.groupName);
        // let b = `groupId=${this.custGroupInfoBean.groupId};groupName=${a}`;
        // window.open('/#/pages/tzb/custom/custom-group/custom-group-view/custom-image-query;' + b);
        debugger
        if (!this.globalSeqNoImage || this.globalSeqNoImage == 'undefined') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '无此附件' }];
            return
        }
        //获取用户名及ID
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        let orgID = commonHeader.orgId;
        let userId = commonHeader.userId;
        let orgName = chName.orgName;
        let userName = chName.userName;
        let time = new Date();
        let year = time.getFullYear();
        let month = time.getMonth() + 1 + '';
        let datas = time.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (datas.length < 2) {
            datas = "0" + datas
        }
        let QUERY_TIME = '' + year + month;
        window.open(TZB_HTTP_YXKQ + "SunIAS_V7/SunIASRequestServlet.do?UID=MOBILE&OrgID=" + orgID + "&OrgName=" + orgName + "&OrgOwner=" + orgID + "&right=0100000&PWD=cyg&UserID=" + userId + "&UserName=" + userName + "&AppID=SA&info1=BUSI_SERIAL_NO:" + this.globalSeqNoImage + ";OBJECT_NAME:APP1009;FILELEVEL:33;RIGHT:1111111;OBJECT_NAME:APP1009;QUERY_TIME:" + QUERY_TIME)
    }

    //批量导入名单
    importList() {
        this.batchDisplay = true;
    }

    selectCustGroupInfo() {
        // 客户群信息查询
        this.param = {
            groupId: this.route.snapshot.params['groupId'],
            applyType: this.route.snapshot.params['applyType'],
        }
        this.httpService.selectCustGroupInfo(this.param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.custGroupInfoBean = data;
                this.custGroupInfoBean.contactList = data.contactList;
                this.custGroupInfoBean.mentorList = data.mentorList;
                this.contactArr = data.contactList;//群联系人
                this.introducerArr = data.introducerList;//群介绍人
                this.mentorRoleArr = data.mentorList;//指导师傅
                // if (this.custGroupInfoBean.appraisalList) {
                //     this.appraisalRoleName = this.custGroupInfoBean.appraisalList[0].roleName
                // }
                this.custGroupBean.ownerId = data.ownerId;
                this.custGroupBean.ownerName = data.ownerName;
                this.custGroupInfoBean.region = data.region;//四级区划
                if (data.region && data.region != '') {
                    this.queryParentZoneAll(data.region);
                }
                this.parentZoneInfoBean.zoneId = this.custGroupInfoBean.region;//四级区划 、
                if (data.subGroupType && data.subGroupType != '') {
                    this.queryTaskByIndtstryAll(data.subGroupType);
                }
                // 群联系人
                let arr = [];
                if (data.contactList) {
                    data.contactList.forEach(item => {
                        arr.push(item.roleName);
                    });
                }
                this.contactRoleName = arr.join(';')
                // 群介绍人
                let arr1 = [];
                if (data.introducerList) {
                    data.introducerList.forEach(item => {
                        arr1.push(item.roleName);
                    });
                }
                this.introducerRoleName = arr1.join(';')
                // 根据删除原因是否有值判断属于哪种审批
                if (data.deleteReason && data.deleteReason != '' && data.deleteReason != null && data.deleteReason != undefined) {
                    this.opFlag = true;
                    this.deleteReason = data.deleteReason;
                    this.selectGroupAdminInfoContact();
                    this.selectGroupAdminInfoIntru();
                } else {
                    this.opFlag = false;
                }
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询信息失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code }];

            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }]
        })
    }
    queryParentZoneAll(region) {
        let params = {
            zoneId: region
        }
        let successFnc = data => {
            data.firstZone.forEach(item => {
                this.regionSHeng.push({
                    label: item.zoneName,
                    value: item.zoneId
                })
            });

            data.secondZone.forEach(item => {
                this.regionCity.push({
                    label: item.zoneName,
                    value: item.zoneId
                })
            });

            data.thirdZone.forEach(item => {
                this.regionArea.push({
                    label: item.zoneName,
                    value: item.zoneId
                })
            });

            data.fourthZone.forEach(item => {
                this.regionStreet.push({
                    label: item.zoneName,
                    value: item.zoneId
                })
            });

            this.regionSHeng = _.clone(this.regionSHeng)
            this.regionCity = _.clone(this.regionCity)
            this.regionArea = _.clone(this.regionArea)
            this.regionStreet = _.clone(this.regionStreet)
            data.zoneList.forEach(item => {
                switch (item.parentZoneLevel) {
                    case '1': this.regionSHeng1 = item.parentZoneId; break;
                    case '2': this.regionCity1 = item.parentZoneId; break;
                    case '3': this.regionArea1 = item.parentZoneId; break;
                    case '4': this.regionStreet1 = item.parentZoneId; break;
                    default: break;
                }
            })
        }
        this.commfunc.interfaceComFnc(this.httpService.queryParentZoneAll(params), successFnc, this.msgs)
    }


    // 查询群联系人
    selectGroupAdminInfoContact() {

        let parames = {
            groupId: this.route.snapshot.params['groupId'],
            roleType: '1',
            statusId: 1
        }
        this.httpService.selectGroupAdminInfo(parames).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.contactArr = data.roleList || [];
                let arr = [];
                if (data.roleList) {
                    data.roleList.forEach(item => {
                        arr.push(item.userName);
                    });
                }
                this.contactRoleName = arr.join(';')
            }
        })
    }

    // 查询群介绍人
    selectGroupAdminInfoIntru() {

        let parames = {
            groupId: this.route.snapshot.params['groupId'],
            roleType: '2',
            statusId: 1
        }
        this.httpService.selectGroupAdminInfo(parames).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.introducerRoleName = data.roleList || [];
                let arr = [];
                if (data.roleList) {
                    data.roleList.forEach(item => {
                        arr.push(item.userName);
                    });
                }
                this.introducerRoleName = arr.join(';')
            }

        })
    }

    //投向行业回显
    queryTaskByIndtstryAll(subGroupType) {
        let params = {
            taskTypeId: subGroupType, taskTypeKey: 'indtstryType'
        };
        this.httpService.queryTaskByIndtstryAll(params).subscribe(data => {
            this.firstToinvestTradeOptions = this.commfunc.listToLabelvalue(data.firstTask, 'taskTypeName', 'taskTypeId');
            this.firstToinvestTradeOptions.unshift({ label: '请选择', value: '' });
            this.secondToinvestTradeOptions = this.commfunc.listToLabelvalue(data.secondTask, 'taskTypeName', 'taskTypeId');
            this.secondToinvestTradeOptions.unshift({ label: '请选择', value: '' });
            this.thirdToinvestTradeOptions = this.commfunc.listToLabelvalue(data.thirdTask, 'taskTypeName', 'taskTypeId');
            this.thirdToinvestTradeOptions.unshift({ label: '请选择', value: '' });
            this.fourthToinvestTradeOptions = this.commfunc.listToLabelvalue(data.fourthTask, 'taskTypeName', 'taskTypeId');
            this.fourthToinvestTradeOptions.unshift({ label: '请选择', value: '' });
            data.taskList.forEach(item => {
                switch (item.level) {
                    case '1': this.firstToinvestTrade = item.taskTypeId; break;
                    case '2': this.secondToinvestTrade = item.taskTypeId; break;
                    case '3': this.thirdToinvestTrade = item.taskTypeId; break;
                    case '4': this.fourthToinvestTrade = item.taskTypeId; break;
                    default: break;
                }
            })
        })
    }

    // 客户群名单批量导入关闭
    closebatch() {
        this.batchDisplay = false;
    }

    // 客户群名单返回结果模态框
    returnCustGroupList() {
        this.addDisplay = false;
    }

    //批量导入
    onBeforeSend(event) {
        // 上传点击,需跨域
        this.custGroupListBean = new CustGroupListBean();
        if (event.xhr.response) {
            this.fileList = JSON.parse(event.xhr.response);
            if (this.fileList.length > 1) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '不支持上传多个文件！' }];
                return;
            }
            if (this.fileList.hasOwnProperty('errorMsg')) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
                return
            }

            this.custGroupListBean.groupId = this.groupId;
            this.custGroupListBean.filePath = this.fileList[0].fileUrl;
            this.custGroupListBean.fileName = this.fileList[0].fileName;
        }
        else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '获取服务器列表失败！' }];
        }

        //不需要审批
        let param = this.custGroupListBean;
        this.custGroupListBean.groupApplyFlag = '1';
        this.httpService.batchCustGroupList(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.custGroupListBean.successNum = data.successNum;
                this.custGroupListBean.failNum = data.failNum;

                data.failInfoList = data.failInfoList.filter((item) => { return item.executeFlag == 'F' });//
                this.custGroupListBean.failInfoList = data.failInfoList;
                this.batchDisplay = false;
                this.addDisplay = true;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '导入失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
}
