import { Component, OnInit } from '@angular/core';
import { ConfirmDialogModule, ConfirmationService, Message } from 'primeng/primeng';
import { SelectLoanApplyRegisInfoBean } from './bean/selectLoanApplyRegisInfo.bean';
import { AddLoanApplyRegisInfoBean } from './bean/addLoanApplyRegisInfo.bean';
import { UpdateLoanApplyRegisInfoBean } from './bean/updateLoanApplyRegisInfo.bean';
import { CusOperationHttpService } from '../../../custom/http/custom-operation/custom-operation.http.service';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DecimalPipe } from '@angular/common';
@Component({
    selector: 'loans',
    templateUrl: './loans.component.html',
    styleUrls: ['./loans.component.scss'],
    providers: [CusOperationHttpService, ConfirmationService, Commfunc, DecimalPipe]
})
export class LoansComponent implements OnInit {
    code: any;
    listMoney: any;
    parentValue: string;
    first: number = 0;
    operateName: any;
    managerName: any;
    operateOrgIdName: any//修改
    operateOrgName: any;//经办机构名称  新增
    num: number;

    date: Date = new Date();
    dateEnd: any;
    treePage: boolean = false;

    userform: FormGroup;
    ch: any = CALENDAR_CN;
    operateDateQuery: any;//查询经办日期

    startCridetDateDetail: any;//详情起贷日期
    endCridetDateDetail: any;//详情到期日期
    operateDateDetail: any;//详情经办日期

    total: string;
    loantable: any;
    msgs: Message[] = [];


    detailPage: boolean = false;
    detailHeader: any;
    title: string = '贷款申请';
    bussTypeOptions: any ;//业务品种
    guaranteeModeOptions: any ;//主担保方式
    sourceTypeOptions: any ;//来源类型
    danbaoOptions: any = [];
    sourceChanelOptions: any ;
    applyStatusOptions: any ;//申请状态
    YingxiaoTJOptions: any ;//营销途径
    explain: boolean = false;//说明


    XYTJ: boolean = false;//营销途径


    items: any;//产品名称
    paramshow: any;//产品value
    //详情页面数据
    dataMsg: any = {
        custNo: '',
        custName: '',
        bussType: '',
        guaranteeMode: '',
        startCridetDate: '',
        endCridetDate: '',
        applyAMT: '',
        sourceType: '',
        intrCustNo: '',
        intrUserName: '',
        intrGlobalId: '',
        intrMobileNo: '',
        discription: '',
        sourceChanel: '',
        managerId: '',
        operateId: '',
        operateDate: '',
        operateOrgIdName: '',
        applyStatus: '',
        seqNo: '',
        servSeqNo: ''

    };
    treeDate: any = [];
    selectLoanApplyRegisInfoBean: SelectLoanApplyRegisInfoBean = new SelectLoanApplyRegisInfoBean();
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    params: any;
    applyAmt: string;
    constructor(
        public httpService: CusOperationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
        public routeInfo: ActivatedRoute,
        public router: Router,
        public decimalPipe: DecimalPipe
    ) {
        //报文头
        this.selectLoanApplyRegisInfoBean['taskCategoryId'] = this.routeInfo.snapshot.params.taskCategoryId;
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        this.userform = fb.group({
            custNo: ['', Validators.required],
            custName: ['', Validators.required],
            startCridetDate: [''],
            endCridetDate: [''],
            seqNo: [''],
            sourceChanel: [''],
            intrCustNo: [''],
            intrUserName: [''],
            intrGlobalId: [''],
            intrMobileNo: [''],
            discription: [''],
            marketType: [''],
            applyAmt: ['', Validators.required],
            guaranteeMode: ['', Validators.required],
            managerId: ['', Validators.required],
            operateId: ['', Validators.required],
            operateOrgName: ['', Validators.required],
            operateDate: ['', Validators.required],
            applyStatus: ['', Validators.required],
            sourceType: ['', Validators.required],
            itemsAdd: ['', Validators.required],
        })
        this.operateDateQuery = this.date;
    }

    ngOnInit() {
        this.codeValues();
        this.bussTypeOptions = this.code['BussTypes'];
        this.guaranteeModeOptions = this.code['GuaranteeModes'];
        this.sourceTypeOptions = this.code['SourceType'];

        this.sourceChanelOptions = this.code['SourceChanel'];
        this.applyStatusOptions = this.code['ApplyStatus'];
        this.YingxiaoTJOptions = this.code['YingxiaoTJ'];
        this.dateEnd = moment().add(6, 'year').format('Y-MM-DD');
        this.selectLoanApplyRegisInfoBean.applyStatus = '0';
        this.selectLoanApplyRegisInfoBean.pageSize = 10;
        this.selectLoanApplyRegisInfoBean.pageNum = 1;
        this.loantable = [];
    }
    chanpinQry(i) {
        this.num = i;
        this.treePage = true;
    }
    //翻页
    paginate(event) {
        //每页显示的条数
        this.selectLoanApplyRegisInfoBean.pageSize = event.rows * 1;
        //当前页
        this.selectLoanApplyRegisInfoBean.pageNum = event.page + 1;
        this.first = event.page * this.selectLoanApplyRegisInfoBean.pageSize;
        this.query();
    }
    queryFirst(){
        this.selectLoanApplyRegisInfoBean.pageSize = 10;
        this.selectLoanApplyRegisInfoBean.pageNum = 1;
        this.first = 0;
        this.query();
    }
    query() {
        this.selectLoanApplyRegisInfoBean.bussType = this.paramshow;
        this.selectLoanApplyRegisInfoBean.operateDate = this.commfunc.transDateN(this.operateDateQuery);//查询经办日期
        this.httpService.selectLoanApplyRegisInfoList(this.selectLoanApplyRegisInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.loantable = data.applyRegisInfoList;
                this.loantable.forEach(item => {
                    let temp = {
                        productId: item.bussType
                    }
                    this.httpService.getAvailableProduct(temp).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            item.bussTypeName = data.availableProduct.productName;
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                        }
                    }, err => {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                    });
                    if (item.applyStatus == '0') {
                        item.applyStatus = '待发起';
                    } else if (item.applyStatus == '1') {
                        item.applyStatus = '已发起';
                    } else {
                        item.applyStatus = '';
                    }
                    if (item.sourceType == '01') {
                        item.sourceType = '客户介绍';
                    } else if (item.sourceType == '02') {
                        item.sourceType = '手机短信';
                    } else if (item.sourceType == '03') {
                        item.sourceType = '互联网';
                    } else if (item.sourceType == '04') {
                        item.sourceType = '客户经理营销';
                    } else if (item.sourceType == '05') {
                        item.sourceType = '其他';
                    } else if (item.sourceType == '06') {
                        item.sourceType = '续贷';
                    } else {
                        item.sourceType = '';
                    }
                    if (item.sourceChanel == '01') {
                        item.sourceChanel = '手工发起';
                    } else if (item.sourceChanel == '02') {
                        item.sourceChanel = '自助渠道';
                    } else if (item.sourceChanel == '03') {
                        item.sourceChanel = '贷款到期待处理转入';
                    } else if (item.sourceChanel == '04') {
                        item.sourceChanel = '后台中心预授信';
                    } else if (item.sourceChanel == '05') {
                        item.sourceChanel = '预授信任务';
                    } else {
                        item.sourceChanel = '';
                    }
                });
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    chongzhi() {
        this.items = '';
        this.paramshow = '';
        this.selectLoanApplyRegisInfoBean.applyAmt = '';
        this.selectLoanApplyRegisInfoBean.custNo = '';
        this.selectLoanApplyRegisInfoBean.custName = '';
        this.operateDateQuery = '';
        this.selectLoanApplyRegisInfoBean.operateDate = '';
        this.selectLoanApplyRegisInfoBean.seqNo = '';
        this.selectLoanApplyRegisInfoBean.sourceChanel = '';
        this.selectLoanApplyRegisInfoBean.sourceType = '';
    }
    //新增
    add() {
        this.router.navigate(["pages/tzb/workflow/custom-workflow/addloan"])
    }

    //详情
    detail(item) {
        this.detailPage = true;
        this.detailHeader = '贷款申请详情';
        let param = { "seqNo": item.seqNo };
        this.httpService.selectLoanApplyRegisInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataMsg = data;
                this.dataMsg.forEach(item => {
                    let temp = {
                        productId: item.bussType
                    }
                    this.httpService.getAvailableProduct(temp).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            item.bussTypeName = data.availableProduct.productName;
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                        }
                    }, err => {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                    });
                });
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //删除
    delete(item) {
        this.confirmationService.confirm({
            message: '是否确认删除?',
            header: '删除',
            icon: 'fa fa-trash',
            accept: () => {
                let parm = { "seqNo": item.seqNo };
                this.httpService.deleteLoanApplyRegisInfo(parm).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'info', summary: '确认删除', detail: '删除成功' }];
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '确认删除', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                })

            },
            reject: () => {
                this.msgs = [{ severity: 'info', summary: '取消删除', detail: '删除已取消' }];
            }
        });
    }
    //修改
    update(item) {
        this.router.navigate(["pages/tzb/workflow/custom-workflow/updateloan", item])
    }

    detailCancel() {
        this.detailPage = false;
    }
    money() {
        this.applyAmt = this.decimalPipe.transform(this.selectLoanApplyRegisInfoBean.applyAmt, '1.2-2');

        console.log(this.decimalPipe.transform(this.selectLoanApplyRegisInfoBean.applyAmt, '1.2-2'), this.decimalPipe.transform(123444, '1.2-2'));

        if (!this.listMoney) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请先选择产品' }];
        } else {
            if (parseInt(this.selectLoanApplyRegisInfoBean.applyAmt) < this.listMoney[0].min * 1) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '不能小于' + this.listMoney[0].min }];
            } else if (parseInt(this.selectLoanApplyRegisInfoBean.applyAmt) > this.listMoney[0].max * 1) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '不能大于' + this.listMoney[0].max }];
            }
        }
        this.method();
    }
    method() {
        console.log(this.applyAmt);
        this.selectLoanApplyRegisInfoBean.applyAmt = this.applyAmt;
    }

    getData(event) {
        this.treePage = false;
        this.parentValue = event;
        this.items = this.parentValue[0]['productName'];
        this.paramshow = this.parentValue[0]['productId'];
        let arrDanbao = this.parentValue[3];
        this.danbaoOptions = [];
        this.danbaoOptions.push({ label: '请选择', value: '' })
        for (let item of arrDanbao) {
            this.danbaoOptions.push({ label: item['productFeatureName'], value: item['productFeatureId'] })
        }
        this.listMoney = this.parentValue[1];
        let listTime = this.parentValue[2];
        if (this.selectLoanApplyRegisInfoBean.applyAmt) {
            this.money();
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}