import { Component, OnInit, OnChanges, Output, EventEmitter } from '@angular/core';
import { TreeNode, Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ActivatedRoute, Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { RadioButtonModule } from 'primeng/primeng';
import { DealHttpService } from "app/pages/tzb/order/http/deal.http.service";
import { ProductManageHttpService } from '../../../../product-manage/http/product-manage.http.service';
import { ProductDirectoryBean } from '../../../../product-manage/product-summary/product-directory/bean/product-directory.bean';

@Component({
    selector: 'loansTree',
    templateUrl: './loansTree.component.html',
    styleUrls: ['./loansTree.component.scss'],
    providers: [ProductManageHttpService, Commfunc, DealHttpService]
})


export class LoansTreeComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //全选/单选
    selectedFiles;
    selectedAll: any = [];
    checkAll: boolean = false;
    checkBox: any = [];
    tabMesg: any = tableMessage;//列表无数据
    //总页数
    total: string;
    //每页多少条
    pageSize: number = 10;
    //每页多少条
    pageNum: number = 1;
    head_title: string = '业务目录详情';
    //提示信息
    msgs: Message[] = [];
    //组件的参数
    updateValue: any = [];

    //列表
    cars_2: any = [];
    treeData: any;
    left_tree: any = [];
    left_tree_arr: any = [];
    hierarchy_arr: any = [];
    ctlgIdValue: any;
    all_tree; any = [];
    first_meu_obj: any = {};//根目录id 组 左侧
    first_meu_left: string = '';//左侧根目录值
    ctlgId: string;
    ctlgName: string;
    ctlgDsc: string;
    belongingDepartment: string;//归属部门
    marketingDepartment: string;//销售部门
    salesStaffPost: string;//销售岗位

    //模态框
    showModel: string;
    headerTitle: string
    display: boolean = false;

    ctlgIdV: string = '';
    maxIdLevelV: number;
    ctlgIdLevelV: number;
    treeDate: any = [];
    treeDate2: any = [];
    first_id: string;
    second_id: string;


    businessDirectoryBean: ProductDirectoryBean = new ProductDirectoryBean();
    constructor(public productPlatformHttpService: ProductManageHttpService,
        public commfunc: Commfunc,
        public router: ActivatedRoute,
        public route: Router,
        public dealHttpService: DealHttpService,
    ) {
    }

    ngOnInit() {
        let a = this.router.params;
        this.queryCatalog();
        // this.query_line();

    }
    //调用公共参数项服务查询业务目录编码
    queryCatalog() {
        let param = { paramGroupId: "COP", paramName: "ctlgIdValue" }
        this.dealHttpService.queryCommonParamItemService(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                console.log(data)
                // this.first_id = data.returnCommonParamItemList[0].paramItemName;
                for (let item of data.returnCommonParamItemList) {
                    if (item.paramItemValue == '贷款指标') {
                        this.second_id=item.paramItemName
                    }else if(item.paramItemValue=='贷款产品目录参数'){
                        this.first_id=item.paramItemName
                    }
                }
                this.ctlgIdValue = this.first_id;
                this.query_line();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })

    }
    query_line() {
        let this_ = this;
        let parm = { ctlgId: this.ctlgIdValue, categoryNum: this.second_id, pageSize: 10000 }
        this.productPlatformHttpService.ctlgMenuQuery(parm).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.all_tree = data.customerMenuList;
                this.left_tree_arr = data.customerMenuList;
                this.left_tree = this.hierarchyTree(data.customerMenuList, '');
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    nodeCheck(event) {
        this.hierarchy_arr = this.getMeuInfo(this.left_tree_arr, event.node.value[0])
        if (event.node.parent != undefined) {
            this.ctlgIdV = event.node.value[0];
            this.maxIdLevelV = this.hierarchy_arr[1];
            this.ctlgIdLevelV = this.hierarchy_arr[0] - 1;
            this.productQuery();
        }
    }
    //+号点击
    nodeExpend(event) {
        let this_ = this;
        let parames = {
            ctlgId: this.ctlgIdValue,
            categoryNum: '001',
        }
        let delData = function (data) {
            event.node.children = this_.hierarchyTree(data.customerMenuList, event.node.value[0]);
        }
        if (event.node.value[1] === 1) {
            this.interfaceComFnc(this.productPlatformHttpService.ctlgMenuQuery(parames), delData);
        } else {
            this.interfaceComFnc(this.productPlatformHttpService.ctlgMenuQuery(parames), delData);
        }
    }

    interfaceComFnc(service_method, success_fnc) {
        service_method.subscribe(data => {
            if (data.returnCode != null && data.returnCode.code == "success") {
                if (success_fnc !== null) {
                    success_fnc(data);
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        },
            error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
            });
    }
    //层级树
    hierarchyTree(menu_list, pid) {
        let menu_arr = [];
        for (let key of menu_list) {
            if (key.pid === pid) {
                menu_arr.push({
                    children: [{}],
                    label: key.name,
                    value: [key.id, key.pid]
                });
            }
        }
        return menu_arr;
    }
    getMeuInfo(tree, curren_pid) {//一级菜单  主目录id
        let count = 1;
        let getFirstId = (tree) => {
            let id = '';
            tree.every(item => {
                item.pid == '' ? id = item.id : null;
            });
            return id;
        }
        //末级菜单id
        let getLastId = (tree) => {
            let id_arr = [];
            tree.forEach(item => {
                let stu = tree.every(item2 => {
                    if (item.id != item2.pid) {
                        return true;
                    }
                }) ? id_arr.push(item.id) : null;
            });
            return id_arr;
        }
        //最大层级深度 
        let getMaxHierarchy = (tree, first_id, last_arr) => {
            let arr = [];
            last_arr.forEach(item => {
                count = 1;
                let max = currentHierarchy(tree, first_id, item)
                arr.push(max);
            });
            return arr.sort()[arr.length - 1];
        }
        //当前层级深度
        //记录层级次数
        function currentHierarchy(tree, first_id, pid) {
            if (pid != first_id) {
                tree.some(item => {
                    if (item.id === pid) {
                        count++;
                        currentHierarchy(tree, first_id, item.pid);
                        return false;
                    }
                });

            }
            return count;
        }

        let first_id = getFirstId(tree);//一级菜单id
        let last_arr = getLastId(tree);//末级菜单 id组
        let current_hierarchy = currentHierarchy(tree, first_id, curren_pid);//当前层级
        let max_hierarchy = getMaxHierarchy(tree, first_id, last_arr);//最大层级 
        return [current_hierarchy, max_hierarchy]
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        //调用查询的方法
        this.productQuery();
    }
    productQuery() {
        let parames = {
            pageSize: this.pageSize,
            pageNum: this.pageNum,
            ctlgId: this.ctlgIdV,
            maxIdLevel: this.maxIdLevelV,
            ctlgIdLevel: this.ctlgIdLevelV
        }
        let this_ = this
        let delData = (data) => {
            this.total = data.total;
            this.cars_2 = data.customerListtemp;
            this.ctlgId = data.departmentList.departmentList[0].basicMessage.catalogOrCategoryId;
            this.ctlgName = data.departmentList.departmentList[0].basicMessage.ctlgOrCategoryName;
            this.ctlgDsc = data.departmentList.departmentList[0].basicMessage.ctlgOrCategoryDec;
        }
        this.interfaceComFnc(this.productPlatformHttpService.ctlgManageContactAttrInfo(parames), delData);
    }
    cusCreate(item) {

        let param = {
            productId: item.productId
        }
        this.productPlatformHttpService.getAvailableProduct(param).subscribe(data => {
            if (data.productFeatureList && data.productFeatureList.length <= 1) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择贷款类产品' });
                return;
            }
            this.selectedAll = [];
            let param = {
                productName: data.availableProduct.productName,
                productId: data.availableProduct.productId
            }
            let arrMoney = [];
            let timearr = [];
            let listDanbao = [];
            for (let item of data.productFeatureList) {
                if (item.productFeatureCategoryId == "PC00000003") {
                    arrMoney = item.productFeatureTypeList[1].productFeatureList;
                    timearr = item.productFeatureTypeList[3].productFeatureList;
                } else if (item.productFeatureCategoryId == "PC00000005") {
                    listDanbao = item.productFeatureTypeList[0].productFeatureList;
                }
            }
            this.selectedAll.push(param, arrMoney, timearr, listDanbao);
        })


    }
    //确定按钮事件
    addBtnCom() {
        this.outValue.emit(this.selectedAll);
    }
    //返回
    comeback() {
        this.outValue.emit(this.selectedAll);
    }
    // 详情
    basicDetails(item) {
        this.display = true;
        this.headerTitle = '可售产品详情';
        this.showModel = '1';
        this.updateValue = item.productId;
    }
    detailsCall(param) {
        this.display = param;
    }
}
