import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { CustomWorkflow } from './custom.component';//客户工作流
import { AssociationRelation } from './association-relation/association-relation';//关联关系
import { LoansComponent } from './loans/loans.component';//贷款
import { AddLoanComponent } from '../custom/loans/addloan/addloan.component';
import { UpdateLoanComponent } from '../custom/loans/updateloan/updateloan.component';
// import { CreateCustomGroupApprovalComponent } from './custom-group/custom-group.component';//客户群

const routes: Routes = [
    {
        path: '',
        component: CustomWorkflow,
        children: [
            { path: '', redirectTo: 'association-relation' },
            {
                path: 'association-relation', component: AssociationRelation,
            },
            {
                path: 'addloan',
                component: AddLoanComponent
            },          
            {
                path:'updateloan',
                component:UpdateLoanComponent
            },
            // {
            //     path: 'custom-group', component: CreateCustomGroupApprovalComponent,
            // },
            {
                path: 'loans',
                component: LoansComponent
            }
        ]
    },
];

export const CustomWorkflowRouting: ModuleWithProviders = RouterModule.forChild(routes);