import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { CusViewHttpService } from '../../../../tzb/custom/http/custom-view/custom-view.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-association-relation',
  templateUrl: './association-relation.html',
  styleUrls: ['./association-relation.scss'],
  providers: [CusViewHttpService, Commfunc]
})
export class AssociationRelation implements OnInit {

  msgs: Message[];
  title: string = '关联关系审核';
  custNo: string;
  relationshipCustNo: string;
  relationshipType: string;
  applyType: string;
  item: any = [];
  scheduleId: string;
  remark: string;
  panduan: any[];
  statusTypeOptions: any;
  userName: any;
  userOrgName: any;
  code: any;
  relation_Param: any;
  constructor(
    public activatedRoute: ActivatedRoute,
    public cusViewHttpService: CusViewHttpService,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.panduan = this.code['DomPanduan'];
    this.statusTypeOptions = this.code['ReductionStatus']
    this.relation_Param = JSON.parse(this.commfunc.getSessionDataCH('relationParams'));
    console.log(this.relation_Param);
    this.custNo = this.relation_Param.relationId;
    this.relationshipCustNo = this.relation_Param.relationshipCustNo;
    this.relationshipType = this.relation_Param.relationshipType;
    this.applyType = this.relation_Param.applyType;
    // this.custNo = this.activatedRoute.snapshot.paramMap.get('relationId');
    // this.relationshipCustNo = this.activatedRoute.snapshot.paramMap.get('relationshipCustNo');
    // this.relationshipType = this.activatedRoute.snapshot.paramMap.get('relationshipType');
    // this.applyType = this.activatedRoute.snapshot.paramMap.get('applyType');
    let param = {}
    param = {
      applyType: this.applyType,
      custNo: this.custNo,
      relationshipCustNo: this.relationshipCustNo,
      relationshipType: this.relationshipType,
    };
    let ob = this.cusViewHttpService.selectPartyRelationshipDetail(param)
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.reductionNumberType == '0') {
          data.reductionNumberType = '账户';
        } else if (data.reductionNumberType == '1') {
          data.reductionNumberType = '客户';
        }
        this.item = data;
        this.cusViewHttpService.queryTellerById({ tellerId: this.item.userId }).subscribe(data => {
          if (data.returnCode.code == 'success') {
            if (data.tellerName != null) {
              this.userName = data.tellerName;
            } else {
              this.userName = this.item.userId;
            }
          } else {
            this.userName = this.item.userId;
          }
        });
        this.cusViewHttpService.queryOrgById({ orgId: this.item.userOrgId }).subscribe(data => {
          if (data.returnCode.code == 'success') {
            if (data.orgName != null) {
              this.userOrgName = data.orgName;
            } else {
              this.userOrgName = this.item.userOrgId;
            }
          } else {
            this.userOrgName = this.item.userOrgId;
          }
        });
      }
    })
  }
  ngOnInit() {

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //驳回
  reject() {
    let param = {
      custNo: this.custNo,
      relationshipCustNo: this.relationshipCustNo,
      relationshipType: this.relationshipType,
      applyType: this.applyType,
      apprResult: '1',
      remark: this.remark
    };
    let ob = this.cusViewHttpService.updatePartyRelationshipStatus(param)
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }

  //确定审核
  confirm() {
    let param = {
      custNo: this.custNo,
      relationshipCustNo: this.relationshipCustNo,
      relationshipType: this.relationshipType,
      applyType: this.applyType,
      apprResult: '0',
      remark: this.remark
    };
    let ob = this.cusViewHttpService.updatePartyRelationshipStatus(param)
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }

  codeValue(val, arr: any = []) {
    let temp = val;
    if (arr.length > 0) {
      arr.forEach(item => {
        if (item['value'] == val) {
          temp = item['label']
        }
      });
    }
    return temp;
  }
}
